.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App{
  position: relative;
  overflow-x: clip;
}


.App>:nth-child(1){
  background: var( --black);
  position: relative;
}

.App .white-gradient{
  position: absolute;
  width: 21rem;
  height: 21rem;
  background: rgb(225, 225, 225, 0.52);
  border-radius: 100px;
  filter: blur(100px);
}